import React, {Component} from "react";
import PropTypes from "prop-types";
import AssistMode from "../../../checkout/components/consents/AssistModeEnum";

class OraLoyaltyHeader extends Component {
    static propTypes = {
        descriptions: PropTypes.shape({
            changeAddressTitle: PropTypes.string,
            changeAddressButtonLabel: PropTypes.string,
            createLeadText1: PropTypes.string,
            createLeadText2: PropTypes.string,
            createLeadButtonSeparator: PropTypes.string,
            createLeadChangeAddressButton: PropTypes.string,
            createLeadButton: PropTypes.string,
        }),
        place: PropTypes.string,
        street: PropTypes.string,
        apartmentNumber: PropTypes.string,
        streetNumber: PropTypes.string,
        ftthAvailabilityDate: PropTypes.string,
        assistModeState: PropTypes.string,
        onClickChange: PropTypes.func,
        onClickCreateLead: PropTypes.func,
    };

    static defaultProps = {};

    onClickChange = (e) => {
        e.preventDefault();
        this.props.onClickChange();
    };

    onClickCreateLead = (e) => {
        e.preventDefault();
        this.props.onClickCreateLead();
    };

    render() {
        const street = this.props.street ? `ul. ${this.props.street}` : '';
        const apartmentNumber = this.props.apartmentNumber ? `/${this.props.apartmentNumber}` : '';
        const address = `${this.props.place}, ${street} ${this.props.streetNumber}${apartmentNumber}`;
        return (
            <div className="u-padding-top-xl u-padding-xl">
                <div className="u-display_table">
                    <div className="u-inline-block u-vertical-middle u-spacing-right">
                        <span aria-hidden="true" className="g-icon g-icon--only g-icon--valid g-icon--xs-s g-greenf-c"/>
                    </div>
                    {
                        this.props.ftthAvailabilityDate
                        ? this.renderChangeAddressOrCreateLead(address)
                        : this.renderChangeAddress(address)
                    }
                </div>
            </div>
        );
    }

    renderChangeAddressOrCreateLead(address) {
        return <div className="u-inline-block u-vertical-middle">
            <p>
                <span>{this.props.descriptions.createLeadText1} </span>
                <span>{address} </span>
                <span>{this.props.descriptions.createLeadText2} </span>
                <span>{this.props.ftthAvailabilityDate}. </span>
                <span className="u-font-bold u-xsmall-block">
                    <a href="#" onClick={(e) => this.onClickChange(e)}>
                       {this.props.descriptions.createLeadChangeAddressButton}
                    </a>
                </span>
                <span> {this.props.descriptions.createLeadButtonSeparator} </span>
                {
                    this.props.assistModeState === AssistMode.ACTIVE
                        ? this.renderTurnOffAssistModeText()
                        : this.renderCreateLeadButton()
                }

            </p>
        </div>;
    }

    renderTurnOffAssistModeText() {
        return <span>
            {this.props.descriptions.turnOffAssistModeText}
        </span>;
    }

    renderCreateLeadButton() {
        return <span className="u-font-bold u-xsmall-block">
                    <a href="#" onClick={(e) => this.onClickCreateLead(e)}>
                       {this.props.descriptions.createLeadButton}
                    </a>
                </span>;
    }

    renderChangeAddress(address) {
        return <div className="u-inline-block u-vertical-middle">
            <p>
                <span>{this.props.descriptions.changeAddressTitle} </span>
                <span className="u-font-bold u-xsmall-block">
                    <span>{address} </span>
                    <a href="#" onClick={(e) => this.onClickChange(e)}>
                       {this.props.descriptions.changeAddressButtonLabel}
                    </a>
                </span>
            </p>
        </div>;
    }
}

export default OraLoyaltyHeader;

